@import "../../global.scss";

.intro{
    background-color: white;
    display: flex;

    @include mobile{
        flex-direction: column;
        align-items: center;
    }

    .left{
        flex: 0.5; //divides page in 2 halves
        overflow: hidden;

        .imgContainer{
            width: 700px;
            height: 700px;
            border-radius: 50%;
            background-color: cornflowerblue;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            margin-left: 10%;
            margin-top: 5%;

            @include mobile{
                align-items: flex-start;
            }
            
            img{
                width: 100%;
                height: 100%;

                @include mobile{
                    width: 100%;
                    height: 100%;
                }
            }
        }
    }
    .right{
        flex: 0.5;
        position: relative;

        .wrapper{
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;

            @include mobile{
                padding-left: 0;
                align-items: center;
              }

            h1{
                font-size: 60px;
                margin: 5px 0;

                @include mobile{
                    font-size: 30px;
                }
            }
            h2{
                font-size: 35px;

                @include mobile{
                    font-size: 35px;
                  }
            }
            h3{
                font-size: 30px;

                @include mobile{
                    font-size: 20px;
                }

                span{
                    font-size: inherit;
                    color: cornflowerblue;
                }

                .ityped-cursor {
                    //Cursor blinking animation
                    animation: blink 0.7s infinite;
                }

                @keyframes blink {
                    50%{
                        opacity: 1;
                    }
                    100%{
                        opacity: 0;
                    }
                }
            }
        }

        a{
            position: absolute;
            bottom: 10px;
            right: 50%;

            img{
                width: 70px;
                animation: arrowBlink 2s infinite;
            }

            @keyframes arrowBlink {
                100%{
                    opacity: 0;
                }
            }
        }
    }
}